body {
  color: #6f6f6f;
  font-family: "Merriweather", serif;
  line-height: 1.6;
  background: #FFFFFF;
  font-size: 1rem;
  overflow-x: hidden;
  transition-property: all;
  transition-duration: 0.5s;
  transition-timing-function: ease;
  transition-delay: 0s;
  position: relative;
  left: 0;
}
@media (min-width: 50em) {
  body {
    font-size: 1.125rem;
  }
}

img {
  max-width: 100%;
  height: auto;
}

h1,
h2,
p {
  margin-top: 0;
  margin-bottom: 1em;
}

h1 {
  line-height: 1;
  font-size: 3.125rem;
  color: #009d9a;
}
@media (max-width: 350px) {
  h1 {
    font-size: 2.5rem;
  }
}

h2 {
  font-size: 2.3rem;
  letter-spacing: 0.15em;
  color: #393939;
  text-transform: uppercase;
  line-height: 1.4;
  font-family: "Six Caps", sans-serif;
  font-weight: 400;
}
@media (min-width: 1000px) {
  h2 {
    font-size: 3rem;
  }
}

.nav {
  background: #009d9a;
  position: fixed;
  right: 0;
  width: 235px;
  height: 100%;
  box-shadow: 0 0 3em rgba(0, 0, 0, 0.15);
  transform: translateX(100%);
  transition: transform 300ms cubic-bezier(0.5, 0, 0.5, 1);
  z-index: 9999;
}
.nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  height: 100vh;
  padding: 1em 2em;
}
.nav li {
  margin: 1rem 0;
}
.nav a {
  font-size: 2rem;
  letter-spacing: 0.15em;
  color: #223843;
  text-transform: none;
  line-height: 1;
  font-family: "Six Caps", sans-serif;
  font-weight: 400;
  text-decoration: none;
  color: #FFFFFF;
}
.nav a:hover {
  color: #223843;
}

.nav__toggle {
  position: absolute;
  top: 2em;
  left: 0;
  transform: translateX(-100%);
  background: #009d9a;
  padding: 1em 0.5em;
  border: 0;
  border-radius: 0.25em 0 0 0.25em;
}

.hamburger {
  display: block;
  position: relative;
  width: 1.5em;
  height: 1px;
  background: #FFFFFF;
  transition: transform 350ms ease-in-out, opacity 200ms linear;
}
.hamburger::before, .hamburger::after {
  width: 1.5em;
  height: 1px;
  background: #FFFFFF;
  transition: transform 350ms ease-in-out, opacity 200ms linear;
  content: "";
  position: absolute;
  left: 0;
}
.hamburger::before {
  bottom: 6px;
}
.hamburger::after {
  top: 6px;
}

.nav--open .nav {
  transform: translateX(0);
}
.nav--open .hamburger {
  transform: rotate(45deg);
}
.nav--open .hamburger::before {
  opacity: 0;
}
.nav--open .hamburger::after {
  transform: rotate(90deg) translate(-6px);
}

.about {
  width: 90%;
  margin: 0 auto;
  max-width: 50em;
  margin-top: 5em;
  margin-bottom: 5em;
}
.about::after {
  content: "";
  display: block;
  clear: both;
}
@media (min-width: 50em) {
  .about h1 {
    margin-top: 0.2em;
  }
}
@media (max-width: 50em) {
  .about h1 br {
    display: none;
  }
}
@media (min-width: 40em) {
  .about__img {
    float: left;
    margin-right: 1em;
  }
}
.about__title {
  margin-bottom: 0.25em;
}
.about__subtitle {
  margin: 0.5em 0;
}
@supports (display: grid) {
  .about {
    display: grid;
    grid-template-columns: [port-start] minmax(11em, auto) [name-start] minmax(7em, auto) [img-end] 2fr [port-end];
    grid-column-gap: 0.5em;
  }
  @media (max-width: 600px) {
    .about {
      grid-template-columns: [port-start] minmax(6em, 1fr) [name-start] minmax(6em, 1fr) [img-end] 2fr [port-end];
    }
  }
  .about__img {
    grid-column: port-start/img-end;
    grid-row: 1/2;
  }
  @media (min-width: 50em) {
    .about__img {
      grid-row: 1/4;
    }
  }
  .about__title {
    grid-column: name-start/port-end;
    grid-row: 1/2;
    align-self: end;
    margin-bottom: 0.5em;
  }
  .about__subtitle, .about__text {
    grid-column: port/port;
  }
  @media (min-width: 50em) {
    .about__subtitle, .about__text {
      grid-column: img-end/port-end;
    }
  }
}

.contact {
  width: 90%;
  margin: 0 auto;
  max-width: 50em;
  padding: 5em 0;
}
.contact form {
  display: flex;
  flex-direction: column;
}
@supports (display: grid) {
  @media (min-width: 50em) {
    .contact form {
      display: grid;
      grid-column-gap: 1em;
      grid-template-areas: "name      email" "message   message" "  .       button";
    }
    .contact form .name {
      grid-area: name;
    }
    .contact form .email {
      grid-area: email;
    }
    .contact form .message {
      grid-area: message;
    }
    .contact form .message textarea {
      height: 10em;
    }
    .contact form .form__button {
      grid-area: button;
    }
  }
}
.contact h1 {
  text-align: center;
}
.contact label {
  display: block;
  margin-top: 1.3em;
  color: #393939;
}
.contact input,
.contact textarea {
  box-sizing: border-box;
  display: block;
  padding: 0.75em;
  width: 100%;
  background-color: #f6f6f7;
  border: 1px solid #E8E9EB;
  font-size: 1.2rem;
}
.contact input:focus,
.contact textarea:focus {
  outline: none;
  border-color: #009d9a;
}
.contact textarea {
  resize: none;
}
.contact .alert {
  border: 1px solid #da1e28;
}
.contact .form__button {
  display: inline-block;
  width: auto;
  margin-top: 1.3em;
  margin-left: auto;
  background-color: #009d9a;
  border: none;
  padding-left: 1.5em;
  padding-right: 1.5em;
  padding-top: 0.5em;
  padding-bottom: 0.5em;
  cursor: pointer;
  font-size: 2.5rem;
  letter-spacing: 0.15em;
  color: #FFFFFF;
  text-transform: uppercase;
  line-height: 1;
  font-family: "Six Caps", sans-serif;
  font-weight: 400;
}
.contact .form__button:hover {
  background-color: #00b7b3;
}
.contact span {
  color: #da1e28;
  font-style: italic;
  top: 0.5em;
  visibility: hidden;
}

.portfolio {
  text-align: center;
  background: #E8E9EB;
  padding: 5em 0;
}
.portfolio__image {
  display: block;
  width: 100%;
}
.portfolio__title {
  color: #FFFFFF;
  font-size: 2rem;
  margin-bottom: auto;
}
.featured .portfolio__title {
  font-size: 3rem;
}
.portfolio__desc {
  display: none;
}
.portfolio__link {
  color: #FFFFFF;
  font-weight: 900;
  font-size: 1.1rem;
  text-decoration: none;
  border-bottom: 2px solid #FFFFFF;
  align-self: flex-start;
}
.portfolio__link::after {
  content: url("/app/img/arrow-white.svg");
  display: inline-block;
  width: 0.8em;
  height: 0.8em;
  margin-left: 0.5em;
}

.portfolio__items {
  display: flex;
  flex-wrap: wrap;
  width: 90%;
  margin: 0 auto;
  max-width: 50em;
}

.portfolio__item {
  position: relative;
  overflow: hidden;
  margin: auto;
  flex: 1 1 20rem;
}
.portfolio__item figcaption {
  position: absolute;
  top: 100%;
  bottom: 0;
  right: 0;
  left: 0;
  padding: 1em;
  background: rgba(0, 157, 154, 0.75);
  color: #FFFFFF;
  text-align: left;
  display: flex;
  flex-direction: column;
  transition: top ease-in-out 250ms;
}
.portfolio__item:hover figcaption, .portfolio__item:focus figcaption {
  top: 0;
}

@supports (display: grid) {
  .portfolio__items {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  @media (min-width: 50em) {
    .portfolio__items {
      grid-template-columns: repeat(4, 1fr);
    }
  }

  .portfolio__item.featured {
    grid-column: 1/span 2;
    grid-row: 1/span 2;
  }

  .featured .portfolio__title {
    font-size: 3rem;
  }
  .featured .portfolio__desc {
    display: block;
  }
}
.portfolio__modal {
  position: fixed;
  z-index: 10;
  top: 2em;
  bottom: 2em;
  left: 2em;
  right: 2em;
  background-color: #E8E9EB;
  box-shadow: 0 0 0 5em rgba(57, 57, 57, 0.5);
  overflow-y: scroll;
  overflow-x: hidden;
  display: none;
}
@media (max-width: 50em) {
  .portfolio__modal {
    top: 1em;
    bottom: 1em;
    left: 1em;
    right: 1em;
  }
}
.portfolio__modal .modal__header {
  background-size: cover;
  background-position: center;
  text-align: left;
  padding: 5em 0;
  margin-bottom: 2em;
}
.portfolio__modal .header__one {
  background-image: url(/app/img/portfolio-01.jpg);
}
.portfolio__modal .header__two {
  background-image: url(/app/img/portfolio-02.jpg);
}
.portfolio__modal .portfolio__title__box {
  padding: 2em 1em;
  background-color: rgba(0, 157, 154, 0.75);
  color: #FFFFFF;
  width: 70%;
}
.portfolio__modal .modal__title {
  font-size: 3rem;
  display: inline-block;
}
.portfolio__modal .modal__title::after {
  content: "";
  display: block;
  height: 1px;
  margin: 0.5em 0;
  background-color: #FFFFFF;
  width: 100%;
}
.portfolio__modal .portfolio__content {
  font-size: 1rem;
  text-align: left;
  width: 90%;
  margin: 0 auto;
  padding-bottom: 2em;
}
.portfolio__modal .portfolio--full--width {
  width: 100vw;
  max-width: initial;
  margin: 2em calc(50% - 50vw);
  height: 20vh;
  object-fit: cover;
}

@media (min-width: 40em) {
  .portfolio--pull--left {
    width: 45%;
    float: left;
    margin-right: 1.5em;
  }

  .portfolio--pull--right {
    width: 45%;
    float: right;
    margin-left: 1.5em;
    padding-bottom: 2em;
  }
}
.portfolio__modal--close {
  border: 0;
  background: #E8E9EB;
  width: 2.2rem;
  height: 2.2rem;
  position: fixed;
  top: 4rem;
  right: 4rem;
  cursor: pointer;
}
@media (max-width: 50em) {
  .portfolio__modal--close {
    top: 2em;
    right: 2em;
  }
}

.close {
  display: block;
  position: relative;
}
.close::before, .close::after {
  width: 1.5em;
  height: 2px;
  background: #393939;
  transition: transform 350ms ease-in-out, opacity 200ms linear;
  content: "";
  position: absolute;
  left: 0;
}
.close::before {
  top: -1px;
  left: -1px;
  transform: rotate(45deg);
}
.close::after {
  top: -1px;
  left: -1px;
  transform: rotate(-45deg);
}
@media (min-width: 50em) {
  .close::before, .close::after {
    top: -2px;
    left: -2px;
  }
}

.modal--is--open {
  display: block;
}

@keyframes modalIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes modalOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.intro {
  display: flex;
  color: rgba(255, 255, 255, 0.7);
}
.intro h2 {
  color: #FFFFFF;
}
.intro h2::after {
  content: "";
  width: 20%;
  height: 1px;
  background: rgba(255, 255, 255, 0.5);
  display: block;
  margin: 1em auto 0;
}
@supports (display: grid) {
  .intro {
    display: grid;
    grid-template-columns: minmax(3em, 1fr) minmax(auto, 35em) minmax(3em, 1fr);
  }
  @media (max-width: 350px) {
    .intro {
      grid-template-columns: minmax(2em, 1fr) minmax(auto, 35em) minmax(2em, 1fr);
    }
  }
}
.intro__middle {
  background: #223843;
  padding: 5em 5vw;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.intro__left img, .intro__right img {
  object-fit: cover;
  min-height: 100%;
  min-width: 100%;
  display: block;
}

.welcome {
  background-image: url(/app/img/header-bg.png);
  background-size: cover;
  background-position: center;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.welcome__header {
  font-size: 1.5rem;
  font-weight: 400;
  color: #223843;
  line-height: 2;
  text-align: center;
}
@media (min-width: 1200px) {
  .welcome__header {
    font-size: 2vw;
  }
}
.welcome__header span {
  font-size: 5.5rem;
  letter-spacing: 0.15em;
  color: #009d9a;
  text-transform: uppercase;
  line-height: 1;
  font-family: "Six Caps", sans-serif;
  font-weight: 400;
  display: block;
  margin-right: -0.16em;
}
@media (min-width: 1200px) {
  .welcome__header span {
    font-size: 7vw;
  }
}
@media (max-width: 400px) {
  .welcome__header span {
    font-size: 4rem;
  }
}
.welcome__header::before, .welcome__header::after {
  content: "";
  display: block;
  width: 60%;
  height: 1px;
  background: #223843;
}
.welcome__header::before {
  margin: 0 auto 2em;
}
.welcome__header::after {
  margin: 2.5em auto 0;
}

.social {
  background-color: #223843;
  padding: 4em 0;
  text-align: center;
}
.social p {
  color: #FFFFFF;
}
.social p:first-child {
  margin-bottom: 2.2em;
}
.social__copyright::before {
  content: "";
  display: block;
  height: 1px;
  background: rgba(255, 255, 255, 0.15);
  margin: 4em auto;
  width: 25%;
}

.social__icons {
  display: flex;
  justify-content: space-between;
  margin: 1.75em auto 4em;
  width: 90%;
  margin: 0 auto;
  max-width: 15em;
}
.social__icons img {
  width: 20%;
  height: 20%;
}
.social__icons img:hover {
  cursor: pointer;
  border-radius: 50%;
  background-color: #3c6276;
}